<template>
  <div class="countdown-container">
    <countdown-card :value="days | addZero" subtext="Days" />
    <countdown-card :value="hours | addZero" subtext="Hours" />
    <countdown-card :value="minutes | addZero" subtext="Minutes" />
    <countdown-card :value="seconds | addZero" subtext="Seconds" />
  </div>
</template>

<script>
import CountdownCard from "./CountdownCard.vue";
let dayExp = 24 * 60 * 60 * 1000;
let hourExp = 60 * 60 * 1000;
let minuteExp = 60 * 1000;
let secondExp = 1000;
let totalDays = 14;
let currentDate = new Date();
let targetDate = new Date(new Date().getTime() + totalDays * dayExp);
export default {
  name: "Countdown",
  components: {
    CountdownCard,
  },
  data() {
    return {
      days: 0,
      hours: 0,
      minutes: 0,
      seconds: 0,
      countdownTime: targetDate - currentDate,
    };
  },
  methods: {
    updateTimer() {
      currentDate = new Date();
      this.countdownTime = targetDate - currentDate;
      this.days = Math.floor(this.countdownTime / dayExp);
      this.hours = Math.floor((this.countdownTime % dayExp) / hourExp);
      this.minutes = Math.floor((this.countdownTime % hourExp) / minuteExp);
      this.seconds = Math.floor((this.countdownTime % minuteExp) / secondExp);
    },
  },
  filters: {
    addZero(value) {
      return value > 9 ? value : `0${value}`;
    },
  },
  mounted() {
    setInterval(() => {
      if (this.countdownTime >= 0) {
        this.updateTimer();
      }
    }, 1000);
  },
};
</script>
