<template>
  <div :style="isMobileView ? 'width:20%' : 'width: 19%'">
    <div class="countdown-card">
      <h2 class="time">{{ value }}</h2>
    </div>
    <p class="subtext">{{ subtext }}</p>
  </div>
</template>

<script>
export default {
  name: "CountdownCard",
  props: {
    value: {
      required: true,
    },
    subtext: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      isMobileView: false,
    };
  },
  methods: {
    getScreenWidth() {
      this.isMobileView = screen.width < 426 ? true : false;
    },
  },
  mounted() {
    this.getScreenWidth();
    document.addEventListener("resize", this.getScreenWidth());
  },
};
</script>
